import React from 'react';
import ndarray from 'ndarray'; // Import ndarray
//import ops from 'ndarray ops'; // Import ndarray ops


import ops from 'ndarray-ops';


const ImageLoader = ({ setResizedImage }) => {
  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const img = new Image();
      const reader = new FileReader();

      reader.onload = (e) => {
        img.src = e.target.result;
      };

      img.onload = () => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');

        // Resize the image to 224x224
        canvas.width = 224;
        canvas.height = 224;
        ctx.drawImage(img, 0, 0, 224, 224);
        
        // Get pixel data from the canvas
        const imageData = ctx.getImageData(0, 0, 224, 224).data;

        // Preprocess the image data
        const processedData = preprocess(imageData, 224, 224);
        
        // You can log or use the processedData as needed
        console.log('Processed Data:', processedData);
        
        // Convert resized image to a data URL and set it
        const resizedDataUrl = canvas.toDataURL();
        //setResizedImage(resizedDataUrl);
      };

      reader.readAsDataURL(file);
    }
  };

  return (
    <div>
      <input type="file" accept="image/*" onChange={handleImageUpload} />
    </div>
  );
};



function preprocess(data, width, height) {
    console.log("starting the preprocess steps");
    const dataFromImage = ndarray(new Float32Array(data), [width, height, 4]);
    console.log(dataFromImage);
    
    const dataProcessed = ndarray(new Float32Array(width * height * 3), [1, 3, height, width]);
    console.log(dataProcessed);
    
    // Normalize 0-255 to (-1)-1
    ops.subseq(dataFromImage.pick(2, null, null), 103.939);
    ops.subseq(dataFromImage.pick(1, null, null), 116.779);
    ops.subseq(dataFromImage.pick(0, null, null), 123.68);
  
    // Realign imageData from [224*224*4] to the correct dimension [1*3*224*224].
    ops.assign(dataProcessed.pick(0, 0, null, null), dataFromImage.pick(null, null, 2));
    ops.assign(dataProcessed.pick(0, 1, null, null), dataFromImage.pick(null, null, 1));
    ops.assign(dataProcessed.pick(0, 2, null, null), dataFromImage.pick(null, null, 0));


    console.log(dataProcessed.data)

    console.log("dataprocessed")
  
    return dataProcessed.data;
  }
  














export default ImageLoader;
